import React from 'react'
import { Container } from 'reactstrap'
import FilterTools from './FilterTools'
import { Routes, Route } from "react-router-dom";
import PokemonMainPage from './pages/PokemonPage';
import PokemonDetails from './pages/PokemonDetails';
import PokemonFilterPage from './pages/PokemonFilterPage';

export default class MainComponent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        return (
            <Container color="light" className="bg-dark pb-5" fluid style={{minHeight: '100vh'}}>
                <FilterTools />
                <Routes>
                    <Route path="/" index element={<PokemonMainPage />} />
                    <Route path="pokemon-details/:pokemonName" element={<PokemonDetails />} />
                    <Route path="pokemon-filter/:filter/:type" element={<PokemonFilterPage />} />
                </Routes>
            </Container>
        )
    }

}