import React, { useEffect } from 'react';
import { Container, Row, Col, Button, } from 'reactstrap';
import { PokemonCard, request_pokemon, text_to_title } from '../shared/functions';

export default function PokemonMainPage(props) {
    const [loading, setLoading] = React.useState(true);
    const [pokemonsData, setPokemonsData] = React.useState([]);
    const [loadingMorePokemon, setLoadingMorePokemon] = React.useState(false);
    useEffect(() => {
          (async () => {
            let pokemons = []
            for (let i = 1; i <= 20; i++) {
                await request_pokemon('pokemon/' + i).then(pokemon => {
                    pokemon !== undefined && pokemons.push(pokemon);  // if pokemon is not undefined, push it to the array

                })
            }            
            setPokemonsData(pokemons);
            setLoading(false);
        })();

    }, [])

    const loadMorePokemon = async () => {
        setLoadingMorePokemon(true);
        let pokemons = []
        for (let i = pokemonsData.length; i <= pokemonsData.length + 20; i++) {
            await request_pokemon('pokemon/' + i).then(pokemon => {
                pokemon !== undefined && pokemons.push(pokemon);  // if pokemon is not undefined, push it to the array

            })
        }
        setPokemonsData(prevState =>  [...prevState, ...pokemons]);
        setLoadingMorePokemon(false);
    }
    return (
        <Container fluid className="pb-5">
            <h1>Pokemon Main Page</h1>
            <Row>
                {
                    loading ?
                    <Col><div className="text-center text-light">loading...</div></Col> :
                    <>
                    {
                        pokemonsData.map(pokemon => 
                            <Col sm="12" md="4" lg="3" key={Math.floor(Math.random() * 999999999)}>
                                <PokemonCard 
                                    pokemon_name={pokemon.name}
                                    pokemon_ability={pokemon.abilities.map(ability => text_to_title(ability.ability.name)).join(', ')}
                                    pokemon_image={pokemon.sprites.front_default}
                                />
                            </Col>
                        )
                    }
                    <Col xs="12" className="mt-4">
                        <div className="d-flex justify-content-center">
                            {
                                loadingMorePokemon ?
                                <span className="text-light">loading more pokemon...</span> :
                                <Button color="secondary" onClick={loadMorePokemon} className="mr-2">
                                    Load More
                                </Button>
                            }
                        </div>
                    </Col>
                    </>
                }
                
            </Row>
        </Container>
    )
}