import { POKEMON_API_URL } from "./constants"
import { Card, CardTitle, CardBody, CardSubtitle, } from 'reactstrap';
import { Link } from 'react-router-dom';

export const request_pokemon = async (request, endpoint_provide = false) => {
    let request_url = endpoint_provide ? request : POKEMON_API_URL + request;
    return await fetch(request_url)
    .then(response => response.json())
    .then(data => data)
    .catch(error => console.log(error))
}

export const PokemonCard = ({pokemon_name, pokemon_image, pokemon_ability}) => {
    return (
        <div>
            <Card className="bg-dark text-light mb-3 shadow">
                <CardBody>
                <CardTitle tag="h5">
                    {text_to_title(pokemon_name)}
                </CardTitle>
                <CardSubtitle
                    className="mb-2 text-muted"
                    tag="h6"
                >
                    {pokemon_ability}
                </CardSubtitle>
                </CardBody>
                <img alt="Card cap" src={pokemon_image} width="100%" />
                <CardBody>
                {/* <CardText>
                    Some quick example text to build on the card title and make up the bulk of the card's content.
                </CardText> */}
                <Link className="card-link btn btn-primary" to={"/pokemon-details/" + pokemon_name}>
                    View Details
                </Link>
                </CardBody>
            </Card>
        </div>
    )
}

export const text_to_title = (text) => {
    return text.split(" ").map((word) => {
        return word.charAt(0).toUpperCase() + word.slice(1)
    }).join(" ")
}