import React, { useEffect, useState } from "react";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem,  } from "reactstrap";
import { request_pokemon } from "./shared/functions";
import { Link } from "react-router-dom";

const MakeDropdown = ({title, headerTitle, items}) => {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    return (
        <Dropdown  isOpen={dropdownOpen}
            toggle={() => setDropdownOpen(!dropdownOpen)}>
            <DropdownToggle className="px-5" outline  caret>
                {title}
            </DropdownToggle>
            <DropdownMenu style={{maxHeight: 300, overflowY: 'auto'}} dark>
            <DropdownItem header>
                {headerTitle}
            </DropdownItem>
            {
                items.map(item => 
                    <Link className="dropdown-item" key={item.url}
                     to={`/pokemon-filter/${item.filter}/${item.type}`}>
                        {item.type}
                    </Link>
                )
            }
            
            </DropdownMenu>
        </Dropdown>
    )
}
export default function FilterTools (props) {
    const [pokemonGenderLists, setPokemonGenderLists] = useState([]);

    useEffect(() => {
        (async () => {
            try {
                let pokemonGenderRequest = await request_pokemon('gender')
                pokemonGenderRequest.results.map(gender => 
                    setPokemonGenderLists(prevState => 
                        [...prevState, {'filter': 'gender', 'type': gender.name, 'url': gender.url}
                    ])
                )

            }catch (err) {
            }
            

        })();
        

    }, [])

    return (
        <div className="d-flex py-3 px-1">
            <div className="text-light d-flex align-items-center" style={{fontWeight: 'bold', marginRight: 10}}>
                <span>Filter</span>
            </div>

            <MakeDropdown 
                title="Gender"
                headerTitle="Filter by Gender"
                items={pokemonGenderLists}
            />          
            
        </div>
    )
}