import React from 'react';
import { Navbar,  NavbarText } from 'reactstrap';

export default function Header(props) {
    return (
        <div className="shadow">
            <Navbar 
                color="dark"
                dark
                expand="md"
                fixed="bottom"
                full
                className="shadow"
            >
                <img className="img-fluid pokemon-logo" src={require('../../assets/pokemon-logo.png').default} 
                     alt="pokemon-logo" />
                
                <NavbarText>
                    &copy; {new Date().getFullYear()} Pokemon Info, All rights reserved. Pokemon info web app 
                    built by Amusa Abayomi (Paulos Ab)
                </NavbarText>
            </Navbar>
            </div>
    )
}