import React, { useEffect } from 'react';
import { useNavigate, useParams } from 'react-router';
import { Container, Row, Col, List, ListInlineItem, Button } from 'reactstrap';
import { request_pokemon, text_to_title } from '../shared/functions';

export default function PokemonDetails(props){
    const params = useParams();
    const [loading, setLoading] = React.useState(true);
    const [loadingText, setLoadingText] = React.useState('Loading...');
    const [pokemonDetails, setPokemonDetails] = React.useState({});
    const navigate = useNavigate();
    useEffect(() => {
        (async () => {
            const request_pokemon_details = await request_pokemon('pokemon/'+params.pokemonName);
            if (request_pokemon_details === undefined) return setLoadingText('Pokemon not found');
            setPokemonDetails(request_pokemon_details);
            setLoading(false);
        })()
    }, [params.pokemonName]);
    return (
        <Container className="text-light pb-5" fluid style={{minHeight: '100vh'}}>
            <Row>
                <Col className="d-flex justify-content-between">
                    <h3>Pokemon Details</h3>
                    <div>
                        <Button color="primary"
                            onClick={() => navigate(-1)}>
                            Back
                        </Button>
                    </div>
                </Col>
                {
                    loading ?
                    <div className="text-light">{loadingText}</div> :
                    <Col sm="12" className="m-auto">
                        <Row>
                            <Col xs="12">
                                <h1 className="text-center">{text_to_title(pokemonDetails.name)}</h1>
                            </Col>
                            <Col sm="10" md="4" className="m-auto">
                                <img src={pokemonDetails.sprites.other.home.front_shiny} 
                                    style={{width: '100%'}} alt="pokemon" />
                            </Col>
                            <Col xs="12">
                                <Row>
                                    <Col sm="4" md="3" className="shadow">
                                        <span>Back Default</span>
                                        <img src={pokemonDetails.sprites.back_default} 
                                            style={{width: '100%'}} alt="pokemon" />
                                    </Col>
                                    <Col sm="4" md="3" className="shadow">
                                        <span>Back Shiny</span>
                                        <img src={pokemonDetails.sprites.back_shiny}
                                            style={{width: '100%'}} alt="pokemon" />
                                    </Col>
                                    <Col sm="4" md="3" className="shadow">
                                        <span>Front Default</span>
                                        <img src={pokemonDetails.sprites.front_default}
                                            style={{width: '100%'}} alt="pokemon" />
                                    </Col>
                                    <Col sm="4" md="3" className="shadow">
                                        <span>Front Shiny</span>
                                        <img src={pokemonDetails.sprites.front_shiny}
                                            style={{width: '100%'}} alt="pokemon" />
                                    </Col>
                                </Row>
                            </Col>
                            
                            <div className="d-flex w-100 my-4 justify-content-between">
                                <div className="m-auto text-light mb-3">
                                    <span className="text-muted h5">Height:</span> {' '}
                                    {pokemonDetails.height}
                                </div>
                                <div className="m-auto text-light mb-3">
                                    <span className="text-muted h5">Weight:</span> {' '}
                                    {pokemonDetails.weight}
                                </div>
                                <div className="m-auto text-light mb-3">
                                    <span className="text-muted h5">Abilities:</span> {' '}
                                    {pokemonDetails.abilities.map(ability => ability.ability.name ).join(', ')}
                                </div>
                                <div className="m-auto text-light mb-3">
                                    <span className="text-muted h5">Experience:</span> {' '}
                                    {pokemonDetails.base_experience}
                                </div>
                            </div>
                            <Col xs="12" className="m-auto text-light">
                                <span className="text-muted h5">Games Indices:</span>
                                <List type="inline">
                                    {pokemonDetails.game_indices.map(game_index =>
                                        <ListInlineItem key={game_index.version.name}
                                            className="shadow py-1 px-2 mb-2"
                                            style={{backgroundColor: 'rgba(80,80,80, 0.3)', borderRadius: 4}}>
                                            {text_to_title(game_index.version.name)} - {game_index.game_index}
                                        </ListInlineItem>
                                    )}
                                </List>

                            </Col>
                            <Col xs="12" className="m-auto text-light">
                                <span className="text-muted h5">Moves:</span>
                                <List type="inline">
                                    {pokemonDetails.moves.map(move =>
                                        <ListInlineItem key={move.move.name}
                                            className="shadow py-1 px-2 mb-2"
                                            style={{backgroundColor: 'rgba(80,80,80, 0.3)', borderRadius: 4}}>
                                            {text_to_title(move.move.name)}
                                        </ListInlineItem>
                                    )}
                                </List>

                            </Col>
                            <Col xs="12" className="m-auto text-light">
                                <span className="text-muted h5">Stats:</span>
                                <List type="inline">
                                    {pokemonDetails.stats.map(stat =>
                                        <ListInlineItem key={stat.stat.name}
                                            className="shadow py-1 px-2 mb-2"
                                            style={{backgroundColor: 'rgba(80,80,80, 0.3)', borderRadius: 4}}>
                                            {text_to_title(stat.stat.name)} - {stat.base_stat}
                                        </ListInlineItem>
                                    )}
                                </List>

                            </Col>
                            <Col xs="12" className="m-auto text-light">
                                <span className="text-muted h5">Types:</span>
                                <List type="inline">
                                    {pokemonDetails.types.map(type =>
                                        <ListInlineItem key={type.type.name}
                                            className="shadow py-1 px-2 mb-2"
                                            style={{backgroundColor: 'rgba(80,80,80, 0.3)', borderRadius: 4}}>
                                            {text_to_title(type.type.name)}
                                        </ListInlineItem>
                                    )}
                                </List>                                

                            </Col>
                        </Row>
                    </Col>
                }
            </Row>
        </Container>
    )
}



