import React, { useEffect } from 'react';
import { useParams } from 'react-router';
import { Container, Row, Col, Button, } from 'reactstrap';
import { PokemonCard, request_pokemon, text_to_title } from '../shared/functions';
import { useNavigate } from 'react-router-dom';

export default function PokemonFilterPage(props) {
    const params = useParams();
    const [loading, setLoading] = React.useState(true);
    const [pokemonFilterData, setPokemonFilterData] = React.useState([]);
    const [loadingMorePokemon, setLoadingMorePokemon] = React.useState(false);
    const allPokemonsFilters = React.useRef([]);
    const pokemonsCounter = React.useRef(0);
    const navigate = useNavigate();
    useEffect(() => {
        (async () => {
            try {
                setPokemonFilterData([])
                let pokemonsFilter = await request_pokemon(params.filter + '/' + params.type)
                let pokemons = []
                for (let i = 0; i < 20; i++) {
                    const pokemon = pokemonsFilter.pokemon_species_details[i];
                    await request_pokemon('pokemon/' + pokemon.pokemon_species.name).then(pokemon => {
                        pokemon !== undefined && pokemons.push(pokemon);  
                    }, 
                    err => console.log(err))
                }
                setPokemonFilterData(pokemons);
                allPokemonsFilters.current = pokemonsFilter.pokemon_species_details;
                pokemonsCounter.current = 20;
                setLoading(false);
                
            } catch (error) {
                console.log(error);                
            }
        })();
    }, [params.type, params.filter])

    const loadMorePokemon = async () => {
        setLoadingMorePokemon(true);
        const pokemons = []
        for (let i = pokemonsCounter.current; i < pokemonsCounter.current + 20; i++) {
            const pokemon = allPokemonsFilters.current[i];
            await request_pokemon('pokemon/' + pokemon.pokemon_species.name).then(pokemon => {
                pokemon !== undefined && pokemons.push(pokemon);   
            })
        }
        setPokemonFilterData(prevState =>  [...prevState, ...pokemons]);
        pokemonsCounter.current = pokemonsCounter.current + 20;
        setLoadingMorePokemon(false);
    }

    return (
        <Container className="text-light pb-5" fluid style={{minHeight: '100vh'}}>
            <Row>
                <Col className="d-flex justify-content-between">
                    <h3>Pokemon Details</h3>
                    <div>
                        <Button color="primary"
                            onClick={() => navigate(-1)}>
                            Back
                        </Button>
                    </div>
                </Col>
                <div className="text-center mb-4">
                    <span style={{fontSize: '210%'}}>{text_to_title(params.filter)}:</span>
                    {' '}
                    <span style={{fontSize: '150%'}}>{text_to_title(params.type)}</span>
                </div>

                {
                    loading ?
                    <div className="text-light">Loading...</div> :
                    <>
                        {
                            pokemonFilterData.map(pokemon => 
                                <Col sm="12" md="4" lg="3" key={Math.floor(Math.random() * 999999999)}>
                                    <PokemonCard 
                                        pokemon_name={pokemon.name}
                                        pokemon_ability={pokemon.abilities.map(ability => text_to_title(ability.ability.name)).join(', ')}
                                        pokemon_image={pokemon.sprites.front_default}
                                    />
                                </Col>
                            )
                        }
                        <Col xs="12" className="mt-4">
                        <div className="d-flex justify-content-center">
                            {
                                loadingMorePokemon ?
                                <span className="text-light">loading more pokemons...</span> :
                                <Button color="secondary" onClick={loadMorePokemon} className="mr-2">
                                    Load More
                                </Button>
                            }
                        </div>
                    </Col>
                    </>
                }
            </Row>
        </Container>
    );
}